#home{
	max-width: 100%;
	font-family: "Montserrat", sans-serif;
}
#ka {
  width: 200px;
  height: 50px;
}
#kb1 {
  border: 1px solid white;
  border-radius: 0px;
  border-right: 0px;
  width: 100px;
}
#kb2 {
  border: 1px solid white;
  border-radius: 0px;
  width: 100px;
  border-left-height: 5px;
}
#kstat {
  background-color: #f3f3f3;
  max-width: 100%;
  padding: 3%;	
  max-height:auto;
}
.empty-div {
  padding-top: 100px;
}
#kstats {
  padding-top: 50px;
}
#kimgrb {
  background-size: 100% auto;
  background-size: cover;
  width: 80%;
  margin: auto 2em;
}
#kp1 {
  font-weight: bold;
  font-size: 25px;
}
#kp2 {
  font-weight: bold;
  font-size: 25px;
}
.flexBox {
  display: flex;
  flex-wrap: wrap;
}
.innerStatsFlex {
  width: 300px;
  margin: 30px 20px;
}
.stats-img{
	width:100%;
}
.media-img{
	width:100%	
}
 .row{
	 margin: auto 50px;
 }
 .col{
	 margin:5%;
 }
  @media only screen and (max-width: 992px) {
.nav{
	flex-direction: column;
}

	
	.stats-img {
		width: 70%;
		
	}
	.row{
		text-align: center;
		max-height: auto;
		padding:20px;
		margin:auto
	}
	.col-12{
		margin: 15px auto;
	}
	.dropdown{
		display: none;
	}
	br{
		display:none
	}
	
  }  
  .innerStatsFlexWrapper {
	width: 90%;
	margin: 0 auto;
	justify-content: center;
  }
 
  #fleet-container{
	background-color: #f3f3f3;
	max-width: 100%;
	padding: 3%;	
	max-height:auto;
  }
  #fleet-inner-container{
	  background-color: white;
	  margin:0%;
	  max-width:100%;

  }
  .fleet-col{
	line-height: 0;
  border-bottom: solid 5px #830e0e;
  text-align: center;
 
 
  
  }
  .fleet-row{
	line-height: 0;
	margin:2%;
	
	border-bottom: solid 5px #aa2727;
  }

  .fleet-row{
	position: relative;
	top: 20px;
	font-weight: 700;
	font-size: 13px;
  }
  .fleet-label{
	position: relative;
	top: 20px;
	font-weight: 700;
	font-size: 13px;
	text-align: center;
  }

.numberBox {
